<template>
  <div>
<!-- <pre>{{ $data|json }}</pre> -->

    <header>
      <a class="btn btn-primary" href="javascript:;" @click="openFileManager"> Add images </a>
      <a class="btn btn-primary" href="javascript:;" @click="addFromUrl"> Add from URL </a>
      <a class="btn btn-default" href="javascript:;" @click="removeSelected" v-show="selected"> Remove selected </a>
      <a class="btn btn-default" href="javascript:;" @click="clearAll" v-show="!selected && images.length > 0"> Clear All </a>
    </header>

    <input v-el:file-uploader type="file" multiple accept="image/*" @change="addFromFile" style="display: none;">

    <section class="image-uploader-container">
      <div v-show="!images.length" class="bg"></div>
      <div class="row">
        <div v-show="images.length" v-for="image in images">
          <div class="col-xs-3">

            <div class="image-uploader-container-item" style="max-height: 150px">

              <div
                @click="setAsPrimary(image)"
                :class="{ primary: image.selected, 'image thumbnail': true }"
                :style="{
                  'background-image': 'url(' + image.src + ')',
                }"
              >

                <div class="image-uploader-container-item-action clearfix">
                  <a href="javascript:void(0);" @click="remove(image)" title="Remove image" class="delete-btn">&times;</a>
                </div>

              </div>
            </div>

          </div>
        </div>
      </div>
    </section>

  </div>
</template>

<script>
  export default {

    data () {
      return {
        images: [],
        inputElement: null,
        fileUploader: null,
        formUploader: null
      }
    },

    ready () {
      this.init()
    },

    computed: {
      selected () {
        for (let i = this.images.length - 1; i >= 0; i--) {
          if (this.images[i].selected) {
            return true
          }
        }
        return false
      }
    },

    methods: {

      init () {
        this.fileUploader = this.$els.fileUploader
      },

      setAsPrimary (image) {
        image.selected = !image.selected
      },

      addFromUrl () {
        const url = window.prompt('Image URL')
        if (!url) return
        const image = new window.Image()
        image.src = url
        image.selected = false
        this.images.push(image)
      },

      addFromFile () {
        const files = this.$els.fileUploader.files
        for (let i = 0; i < files.length; i++) {
          if (i > 7) return

          this.addToList(files[i], i)
        }
      },

      addToList (file, index) {
        const reader = new window.FileReader()

        reader.onloadend = (e) => {
          this.images.push({
            name: file.name,
            size: file.size,
            lastModifiedDate: file.lastModifiedDate,
            lastModified: file.lastModified,
            image_type: file.type,
            src: reader.result,
            selected: false
          })
        }

        reader.onprogress = (e) => {

        }

        reader.readAsDataURL(file)
      },

      openFileManager () {
        this.fileUploader.click()
      },

      clearAll () {
        this.images = []
        this.$els.fileUploader.files = []
      },

      remove (image) {
        this.images.$remove(image)
      },

      removeSelected () {
        for (var i = this.images.length - 1; i >= 0; i--) {
          if (this.images[i].selected === true) {
            this.images.$remove(this.images[i])
          }
        }
      }

    }

  }
</script>


<style scoped>

  .image-uploader-container {
    background: #f0f0f0;
    border: 1px solid #d9d9d9;
    min-height: 360px;
    margin: 10px 0;
    padding: 10px;
    border-radius: 2px;
  }

  .image {
    width: 154px;
    height: 154px;
    background-image: url('http://placehold.it/154');
    background-size: cover;
    background-position: top left;
    cursor: pointer;
  }

  .image.primary {
    border: 6px solid #0E4B8E;
    border-radius: 2px;
  }

  .image:hover .image-uploader-container-item-action {
    display: block;
  }

  .image-uploader-container-item-action {
    display: none;
    /*background: rgba(0, 0, 0, 0.3);*/
    /*margin-bottom: 10px;*/
  }

  .delete-btn {
    background: #F04B59;
    padding: 0 8px;
    color: white;
    text-shadow: 1px 1px 0 #666;
    float: left;
    text-decoration: none;
  }

  .delete-btn:hover {
    background: #F04B90;
  }

  .set-as-primary-btn {
    float: right;
    color: white;
    margin-right: 5px;
    font-size: 12px;
  }
</style>



